.banned-players {
  padding: 60px 20px;
  text-align: center;
  background-color: #2b2b2b;
}

.banned-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.banned-item {
  background-color: #3a3a3a;
  padding: 15px 20px;
  border-radius: 5px;
  width: 300px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  text-align: left;
  border-left: 4px solid #ff4c4c;
}

.player-name {
  font-weight: bold;
  font-size: 16px;
  color: #ff4c4c;
}

.reason {
  margin-top: 5px;
  color: #bbbbbb;
}
