.members {
  padding: 10px 20px;
  text-align: center;
  background-color: #2b2b2b;
}

.member-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

/* Styles */
.member-item {
  background-color: #3a3a3a;
  padding: 10px 15px;
  border-radius: 5px;
  width: 200px;
  text-align: center;
  color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

/* Clan Leader */
.member-item.leader {
  background-color: #444;
  border: 1px solid gold;
  margin: 20px auto;
}

/* Administrators */
.member-item.admin {
  border: 1px solid #ff4c4c;
}

/* Recruits */
.member-item.recruit {
  border: 1px solid #777;
}
