/* Social Media Section Styles */
.social-media {
    padding: 60px 20px;
    text-align: center;
    background-color: #1e1e1e;
  }
  
  .icons {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .icons a img {
    width: 40px;
    height: 40px;
    transition: transform 0.3s;
  }
  
  .icons a img:hover {
    transform: scale(1.1);
  }
  